.main-header__wrapper {
  @media (min-width: $mobile-width-only) {
    padding: 0 20px;
    display: flex;
    position: relative;
  }

  @media (min-width: $tablet-width) {
    width: 185px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    padding: 0;
  }
}

.main-header__logo {
  margin: 0 auto;
  display: block;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    padding: 0;
    display: block;
    box-sizing: border-box;
  }
}

.main-header {
  @media (min-width: $tablet-width) {
    margin: 0 auto;
    padding: 0 20px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    width: 1020px;
    margin: 0 auto;
    height: 56px;
  }
}
